	module.exports = {"data":{"markdownRemark":{"html":"<h1>ВНУТРИ ГЕРБАРИЯ</h1>\n<p>сегодня особенно ветрено<br>\nособенно в чаще<br>\nособенно в чаше<br>\nлеса особенно ветрено</p>\n<p>лежим на жухлой листве<br>\nв обнимку нагие<br>\nв облипку ноги и<br>\nрёбра на мокрой листве</p>\n<p>вокруг закруглились деревья<br>\nшумящим забором<br>\nшумят им за бором<br>\nвселенные будто деревья</p>\n<p>в верху разрыхлилось солнце<br>\nтечёт косыми лучами<br>\nтечёт косуля-м[и э]чами<br>\nв верху расширялось солнце</p>\n<p>а у тебя волосы пальцы<br>\nя собираю из них ладони<br>\nи молюсь на них как итальянцы<br>\nсикстинской мадонне<br>\nа у меня голова тайник<br>\nошибок всего человечества<br>\nты мой безумный тик<br>\nи это никак не лечится<br>\nне выветрится не уйдёт<br>\nа если смотреть с неба<br>\nнаше ложе огромный рот<br>\nгде мы кусочками хлеба<br>\nнасущного днесь разлеглись<br>\nлежим на краю у центра</p>\n<p>Стянулась тучами высь<br>\nи спрятала нас от ветра.</p>"}},"pathContext":{"slug":"lyrics/vnutriGerbariya"}}

